body{
  background-color: #3e3e3e;
}

.todoApp{
  background-color:darkmagenta;
  min-height: 500px;
  margin: 50px;
  width: 400px;
  margin: 40px auto;
}

header{
  position: relative;
  padding: 10px;
  margin-left: 40px;
  border-radius: 10px;
}

header input{
  font-size: 15pt;
  width: 60%;
  padding: 5px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  border-radius: 5px;
}

header button{
  background-color: yellow;
  font-size: 15pt;
  margin-left: 5px;
  border: 0;
  border-radius: 5px;
  padding: 5px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}