.list{
    color: #ffff;
    width: 300px;
    background-color: #bf07ac;
    height: 50px;
    margin: 20px auto;
    border-radius: 5px;
}

.list p{
    padding: 10px;
    padding-top: 12px;
}

.list input{
    background-color: transparent;
    border: 0;
    color: #ffff;
    
}

.list input:focus{
    outline: 0;
    border: 0;
}

.item-icon{
    position: relative;
    top: 4px;
}

.item-icon:hover{
    cursor: pointer;
    color: rgb(55, 250, 250);
}